	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Сгибания рук с гантелями сидя на наклонной скамье не очень популярное, но эффективное упражнение для бицепса. Позволяет сильнее растянуть мышцы, чем при вертикально положении тела. Для выполнения этого упражнения кроме гантелей нужна наклонная силовая скамья. Внимательно следите за техникой выполнения упражнения.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n<li>Установите спинку на силовой скамье под углом (рекомендуется 50-70 градусов) Возьмите в руки гантели с необходимым весом и сядьте на наклонную скамью.</li>\n<li>Исходное положение: руки выпрямлены, опущены вниз по сторонам туловища. Голова прижата к спинке скамьи.</li>\n<li>На выдохе: держа локоть неподвижно, сгибайте руки за счет усилия бицепса.</li>\n<li>На вдохе: верните руки в исходное положение.</li>\n<li>Сделайте требуемое количество повторов.</li>\n</ol>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/z0TqJpouf2k\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/podjem-gantelei-na-biceps-sidya","efficiency":"9","title":"Подъем гантелей на бицепс сидя","image":"/static/3-02-0.png","image_2":"","image_3":"","image_4":"","filterby":"biceps","date":"April 12, 2017","injury":"3","bodypart":"Бицепс","filtertype":"isolated","type":"Изолирующее","filterinventory":"dumbbells","inventory":"Гантели","filterplace":null}}},"pathContext":{}}

